@font-face {
  font-family: 'Dancing Script';
  src: url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500;600&display=swap'),
    url('./font/DancingScript-Bold.ttf');
}
@font-face {
  font-family: 'Pacifico';
  src: url('./font/Pacifico-Regular.ttf');
}

@media screen and (min-width: 350px) {
  .container {
    width: 320px;
    background-image: url('./img/папка.png');
    box-shadow: 5px 5px 5px rgb(66, 67, 67);
  }
}
@media screen and (min-width: 768px) {
  .container {
    width: 700px;
    height: 1800px;
    background-image: url('./img/folder-desktop.png');
    background-repeat: no-repeat;
    background-size: contain;
    box-shadow: none;
  }
  .h2 {
    font-family: 'Dancing Script';
    position: absolute;
    top: 0px;
    left: 430px;
  }
  .h1 {
    font-family: 'Dancing Script';
    width: 50px;
    font-size: 20px;
    position: absolute;
    top: 60px;
    left: 150px;
    transform: rotate(-25deg);
  }
  .task {
    position: absolute;
    top: 50px;
    left: 350px;
    font-family: 'Pacifico';
    font-weight: 200;
  }
}
.container {
  position: relative;

  padding: 10px 10px 20px 10px;

  margin-left: auto;
  margin-right: auto;
}
.filter {
  margin-left: 30px;
  margin-top: 50px;
}
